import API from './dataCountries.js';
import debounce from 'lodash.debounce';
import { alert, error } from '@pnotify/core';
import spotsRef from './spotsRef.js';
import countryCard from '../temp/cards.hbs';
import countriesList from '../temp/list.hbs';
import err from './errMsgDo.js';

const refs = spotsRef();

refs.inputSearch.addEventListener('input', debounce(onInputSearch, 500));

function onInputSearch(element) {
element.preventDefault();
const searchQuery = refs.inputSearch.value;

API.dataCountries(searchQuery)
    .then(countries => {
renderCountryCard(countries);
    })
    .catch(onFetchError);
}
function onFetchError(error) {
  console.log(error);
}

function renderCountryCard(countries) {
  if (countries.length>= 10) {
    return err.errorMsgDet();
  }
  if (countries.length> 1 &&countries.length< 10) {
err.hideError();
refs.countriesList.innerHTML = countriesList(countries);
  }
  if (countries.length === 1) {
err.hideError();
refs.countriesList.innerHTML = countryCard(countries[0]);
  }
  // err.hideError();
  // error("Please enter a more specific query!");
// searchQuery = refs.inputSearch.value;
}
